<style scoped lang="less">
  .toggles {
    display: inline-block;
    padding: 0 45px;
    height: 39px;
    line-height: 39px;
    margin-top: 5px;
    cursor: default;
    background: #aadaff;
    color: #F9FAFC;
    font-size: 18px;
    &:nth-child(1){
      margin-right: -4px;
    }
  }
  .searchInfo-el {
    width: 180px;
    float: right;
  }
  .toggle-act {
    background: #1D8CE0;
  }
</style>
