          {a: '鸿琪医院', b: '2019-10-10', c: '1020360589',expertName: '鸿琪专家',yzk: '皮肤'}
        ],
      }
    },
    props: ['a'],
    components: {

    },
    mounted(){

    },
    methods: {
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
      },
      toggleCW(i){
          this.toggleCWshow = i;
      },
      handleIconClick(){
          alert("搜索")
      },
      aas(i,arr){

      }
    }
  }
</script>



